<template>
    <div class="image-storage-view" v-if="imageStorage.length">
        <page-title :title="pageContent.pageTitle[lang]"/>
        <div class="wrapper">
            <div class="image-list-section">
                <div class="image-item" v-for="(item, i) in imageStorage" :key="i" :class="{'active': (selectedCategory == i)}">
                    <div class="item-head" @click="(selectedCategory == i) ? selectedCategory = -1 : selectedCategory = i">
                        <h4 class="title">{{ item.name + ' (' + item.list.length + ')' }}</h4>
                        <button class="arrow">
                            <span class="fas fa-chevron-down"></span>
                        </button>
                    </div>
                    <div class="item-body" v-if="selectedCategory == i">
                        <table>
                            <tbody>
                                <tr v-for="(image, j) in item.list" :key="j">
                                    <td>
                                        <img :src="image.url">
                                    </td>
                                    <td>
                                        <p class="name">{{ image.name }}</p>
                                        <a :href="image.url" target="_blank">{{ image.url }}</a>
                                    </td>
                                    <td><button @click="toClipboard(image.url)"><span class="fas fa-copy"></span></button></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import PageTitle from '../components/PageTitle.vue';

export default {
    data() {
        return {
            selectedCategory: -1,
            pageContent: {
                pageTitle: {
                    ua: 'Сховище зображень',
                    ru: 'Хранилище изображений',
                    en: 'Image storage'
                }
            }
        }
    },
    components: {
        PageTitle        
    },
    computed: {
        lang() {
            return this.$store.getters['getSelectedLang'];
        },
        imageStorage() {
            return this.$store.getters['getImageStorage'];
        }
    },
    methods: {
        toClipboard(url) {
            let $target = event.target
            $target.classList.add('active');
            $target.parentNode.classList.add('active');
            setTimeout(() => {
                $target.classList.remove('active');
                $target.parentNode.classList.remove('active');
            }, 2000);
            if (navigator.clipboard) {
                navigator.clipboard.writeText(url)
                    .then(() => {
                        
                    })
            }
        }
    },
    created() {
        this.$store.dispatch('fetchImageStorage');
    }
}
</script>